"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table } from "@/components/ui/table"
import { useState } from "react"
import { useBorrowings } from "../hooks/useBorrowings"
import { useBooks } from "../hooks/useBooks"
import { useUsers } from "../hooks/useUsers"

interface Borrowing {
  id: number;
  book_id: number;
  user_id: number;
  borrow_date: string;
  due_date: string;
  return_date?: string;
  status: string;
  fine?: number;
  book_title?: string;
  student_name?: string;
  student_id?: string;
}

const PENALTY_RATE = 1; // RM1 per day

export default function Borrowings() {
  const { borrowings, loading, error, addBorrowing, returnBook, deleteBorrowing } = useBorrowings()
  const { books } = useBooks()
  const { users } = useUsers()
  
  const [showNewBorrowingForm, setShowNewBorrowingForm] = useState(false)
  const [showReturnForm, setShowReturnForm] = useState(false)
  const [selectedBorrowing, setSelectedBorrowing] = useState<Borrowing | null>(null)
  const [penaltyDetails, setPenaltyDetails] = useState({ days: 0, amount: 0 })
  
  const [newBorrowing, setNewBorrowing] = useState({
    book_id: 0,
    user_id: 0,
    borrow_date: "",
    due_date: ""
  })

  const handleNewBorrowing = () => {
    setShowNewBorrowingForm(true)
  }

  const handleReturn = (borrowing: Borrowing) => {
    setSelectedBorrowing(borrowing)
    // Calculate penalty when opening return form
    const today = new Date()
    const dueDate = new Date(borrowing.due_date)
    const diffTime = today.getTime() - dueDate.getTime()
    const diffDays = Math.max(0, Math.ceil(diffTime / (1000 * 60 * 60 * 24)))
    setPenaltyDetails({
      days: diffDays,
      amount: diffDays * PENALTY_RATE
    })
    setShowReturnForm(true)
  }

  const handleDelete = async (borrowingId: number) => {
    if (confirm("Are you sure you want to delete this record?")) {
      try {
        await deleteBorrowing(borrowingId)
      } catch (error) {
        alert("Failed to delete borrowing record")
      }
    }
  }

  const handleAddBorrowing = async () => {
    try {
      await addBorrowing({
        ...newBorrowing,
        status: "Borrowed"
      })
      setNewBorrowing({
        book_id: 0,
        user_id: 0,
        borrow_date: "",
        due_date: ""
      })
      setShowNewBorrowingForm(false)
    } catch (error) {
      alert("Failed to add borrowing")
    }
  }

  const handleProcessReturn = async () => {
    if (!selectedBorrowing) return

    try {
      const today = new Date().toISOString().split('T')[0]
      await returnBook(selectedBorrowing.id, today, penaltyDetails.amount)
      setShowReturnForm(false)
      setSelectedBorrowing(null)
      setPenaltyDetails({ days: 0, amount: 0 })
    } catch (error) {
      alert("Failed to process return")
    }
  }

  const calculateTotalFines = () => {
    return borrowings.reduce((total, borrowing) => total + (borrowing.fine || 0), 0)
  }

  if (loading) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center">Loading...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto p-4">
        <div className="text-center text-red-600">Error: {error}</div>
      </div>
    )
  }

  return (
    <div className="container mx-auto">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-4xl font-bold text-red-950">Borrowings Management</h1>
        <Button 
          className="bg-red-950 hover:bg-red-900 text-white"
          onClick={handleNewBorrowing}
        >
          New Borrowing
        </Button>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Total Borrowings</h3>
          <p className="text-3xl font-bold text-red-950">{borrowings.length}</p>
          <p className="text-sm text-red-700 mt-2">All time</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Active Borrowings</h3>
          <p className="text-3xl font-bold text-red-950">
            {borrowings.filter(b => b.status === "Borrowed").length}
          </p>
          <p className="text-sm text-red-700 mt-2">Currently borrowed</p>
        </Card>
        
        <Card className="p-6 bg-red-50 border-red-100">
          <h3 className="text-lg font-semibold text-red-900">Total Penalties</h3>
          <p className="text-3xl font-bold text-red-950">
            RM {calculateTotalFines()}
          </p>
          <p className="text-sm text-red-700 mt-2">Accumulated fines</p>
        </Card>
      </div>

      {/* Penalty Rate Info */}
      <Card className="p-4 mb-8 bg-yellow-50 border-yellow-200">
        <div className="flex items-center text-yellow-800">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
            <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
          </svg>
          <span>Current Penalty Rate: RM {PENALTY_RATE} per day overdue</span>
        </div>
      </Card>

      {/* Search and Filter */}
      <Card className="p-6 mb-8 border-red-100">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Search
            </label>
            <Input 
              type="text" 
              placeholder="Search by book or student"
              className="border-red-200 focus:border-red-500 focus:ring-red-500"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Status
            </label>
            <select className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500">
              <option value="">All Status</option>
              <option value="borrowed">Borrowed</option>
              <option value="returned">Returned</option>
              <option value="overdue">Overdue</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-red-900 mb-2">
              Date Range
            </label>
            <Input 
              type="date" 
              className="border-red-200 focus:border-red-500 focus:ring-red-500"
            />
          </div>
        </div>
      </Card>

      {/* Borrowings Table */}
      <Card className="border-red-100">
        <div className="overflow-x-auto">
          <Table>
            <thead>
              <tr className="bg-red-50">
                <th className="text-left p-4 text-red-900">Book Title</th>
                <th className="text-left p-4 text-red-900">Student ID</th>
                <th className="text-left p-4 text-red-900">Student Name</th>
                <th className="text-left p-4 text-red-900">Borrow Date</th>
                <th className="text-left p-4 text-red-900">Due Date</th>
                <th className="text-left p-4 text-red-900">Return Date</th>
                <th className="text-left p-4 text-red-900">Status</th>
                <th className="text-left p-4 text-red-900">Penalty (RM)</th>
                <th className="text-left p-4 text-red-900">Actions</th>
              </tr>
            </thead>
            <tbody>
              {borrowings.map((borrowing) => (
                <tr key={borrowing.id} className="border-b border-red-100">
                  <td className="p-4">{borrowing.book_title}</td>
                  <td className="p-4">{borrowing.student_id}</td>
                  <td className="p-4">{borrowing.student_name}</td>
                  <td className="p-4">{borrowing.borrow_date}</td>
                  <td className="p-4">{borrowing.due_date}</td>
                  <td className="p-4">{borrowing.return_date || "-"}</td>
                  <td className="p-4">
                    <span className={`px-2 py-1 rounded-full text-sm ${
                      borrowing.status === "Borrowed" ? "bg-blue-100 text-blue-800" :
                      borrowing.status === "Returned" ? "bg-green-100 text-green-800" :
                      "bg-red-100 text-red-800"
                    }`}>
                      {borrowing.status}
                    </span>
                  </td>
                  <td className="p-4">
                    {borrowing.fine ? (
                      <span className="text-red-600 font-medium">
                        RM {borrowing.fine}
                      </span>
                    ) : "-"}
                  </td>
                  <td className="p-4">
                    <div className="flex gap-2">
                      {borrowing.status === "Borrowed" && (
                        <Button 
                          variant="outline" 
                          className="text-red-950 border-red-200 hover:bg-red-50"
                          onClick={() => handleReturn(borrowing)}
                        >
                          Return
                        </Button>
                      )}
                      <Button 
                        variant="outline" 
                        className="text-red-950 border-red-200 hover:bg-red-50"
                        onClick={() => handleDelete(borrowing.id)}
                      >
                        Delete
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </Card>

      {/* New Borrowing Form Modal */}
      {showNewBorrowingForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">New Borrowing</h2>
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Book
                </label>
                <select 
                  value={newBorrowing.book_id}
                  onChange={(e) => setNewBorrowing({...newBorrowing, book_id: parseInt(e.target.value)})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="">Select Book</option>
                  {books.filter(book => book.status === "Available").map(book => (
                    <option key={book.id} value={book.id}>{book.title}</option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Student
                </label>
                <select 
                  value={newBorrowing.user_id}
                  onChange={(e) => setNewBorrowing({...newBorrowing, user_id: parseInt(e.target.value)})}
                  className="w-full border-red-200 rounded-md focus:border-red-500 focus:ring-red-500"
                >
                  <option value="">Select Student</option>
                  {users.filter(user => user.status === "Active").map(user => (
                    <option key={user.id} value={user.id}>{user.name} ({user.student_id})</option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Borrow Date
                </label>
                <Input 
                  type="date" 
                  value={newBorrowing.borrow_date}
                  onChange={(e) => setNewBorrowing({...newBorrowing, borrow_date: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-red-900 mb-2">
                  Due Date
                </label>
                <Input 
                  type="date" 
                  value={newBorrowing.due_date}
                  onChange={(e) => setNewBorrowing({...newBorrowing, due_date: e.target.value})}
                  className="border-red-200 focus:border-red-500 focus:ring-red-500"
                />
              </div>
              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => setShowNewBorrowingForm(false)}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleAddBorrowing}
                >
                  Add Borrowing
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}

      {/* Return Book Form Modal */}
      {showReturnForm && selectedBorrowing && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
          <Card className="w-full max-w-md p-6 bg-white">
            <h2 className="text-2xl font-bold text-red-950 mb-4">Process Return</h2>
            <div className="space-y-4">
              <div>
                <p className="text-sm text-gray-600">Book Title</p>
                <p className="font-medium">{selectedBorrowing.book_title}</p>
              </div>
              <div>
                <p className="text-sm text-gray-600">Student</p>
                <p className="font-medium">{selectedBorrowing.student_name} ({selectedBorrowing.student_id})</p>
              </div>
              <div>
                <p className="text-sm text-gray-600">Due Date</p>
                <p className="font-medium">{selectedBorrowing.due_date}</p>
              </div>
              
              {/* Penalty Information */}
              <Card className={`p-4 ${penaltyDetails.amount > 0 ? 'bg-red-50' : 'bg-green-50'}`}>
                <h3 className="font-semibold mb-2">Penalty Details</h3>
                <div className="space-y-1">
                  <p>Days Overdue: {penaltyDetails.days}</p>
                  <p>Penalty Rate: RM {PENALTY_RATE}/day</p>
                  <p className="font-bold mt-2">
                    Total Penalty: RM {penaltyDetails.amount}
                  </p>
                </div>
              </Card>

              <div className="flex gap-2 justify-end mt-6">
                <Button 
                  variant="outline" 
                  className="text-red-950 border-red-200 hover:bg-red-50"
                  onClick={() => {
                    setShowReturnForm(false)
                    setSelectedBorrowing(null)
                    setPenaltyDetails({ days: 0, amount: 0 })
                  }}
                >
                  Cancel
                </Button>
                <Button 
                  className="bg-red-950 hover:bg-red-900 text-white"
                  onClick={handleProcessReturn}
                >
                  Process Return
                </Button>
              </div>
            </div>
          </Card>
        </div>
      )}
    </div>
  )
}
