import { useState, useEffect } from 'react'

interface Borrowing {
  id: number;
  book_id: number;
  user_id: number;
  borrow_date: string;
  due_date: string;
  return_date?: string;
  status: string;
  fine?: number;
  book_title?: string;
  student_name?: string;
  student_id?: string;
}

export function useBorrowings() {
  const [borrowings, setBorrowings] = useState<Borrowing[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const fetchBorrowings = async () => {
    try {
      setLoading(true)
      const response = await fetch('/api/borrowings')
      if (!response.ok) throw new Error('Failed to fetch borrowings')
      const data = await response.json()
      setBorrowings(data)
      setError(null)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred')
    } finally {
      setLoading(false)
    }
  }

  const addBorrowing = async (borrowing: Omit<Borrowing, 'id'>) => {
    try {
      const response = await fetch('/api/borrowings', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(borrowing)
      })
      if (!response.ok) throw new Error('Failed to add borrowing')
      const newBorrowing = await response.json()
      setBorrowings(prev => [...prev, newBorrowing])
      return newBorrowing
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to add borrowing')
      throw err
    }
  }

  const returnBook = async (id: number, return_date: string, fine: number) => {
    try {
      const response = await fetch('/api/borrowings', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id, return_date, fine })
      })
      if (!response.ok) throw new Error('Failed to process return')
      const updatedBorrowing = await response.json()
      setBorrowings(prev => prev.map(b => b.id === id ? updatedBorrowing : b))
      return updatedBorrowing
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to process return')
      throw err
    }
  }

  const deleteBorrowing = async (id: number) => {
    try {
      const response = await fetch('/api/borrowings', {
        method: 'DELETE',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id })
      })
      if (!response.ok) throw new Error('Failed to delete borrowing')
      setBorrowings(prev => prev.filter(b => b.id !== id))
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to delete borrowing')
      throw err
    }
  }

  useEffect(() => {
    fetchBorrowings()
  }, [])

  return {
    borrowings,
    loading,
    error,
    addBorrowing,
    returnBook,
    deleteBorrowing,
    refreshBorrowings: fetchBorrowings
  }
}
